"use client";

import { useEffect, useMemo, useState } from "react";
import axiosApi from "@/config/axios-api";
import { AnimatePresence, motion } from "framer-motion";
import { Activity, ChartColumn, ClipboardList, FileCheck2, FilePenLine } from "lucide-react";
import { ErrorBanner } from "@/components/ErrorBanner";
import { addToast, Skeleton } from "@heroui/react";
import EmptyRow from "@/components/dashboard/EmptyRow";
import ServiceCard from "@/components/dashboard/ServiceCard";
import WardRow from "@/components/dashboard/WardRow";
import StatCard from "@/components/dashboard/StatCard";
import ModalWardCard from "@/components/modal/ModalWardCard";
import HospitalHook from "@/hooks/HospitalHook.mjs";
import ModalVNCard from "@/components/modal/ModalVNCard";
import { cryptoDecode, cryptoEncode } from "@/config/crypto.mjs";
import { Tooltip } from "@heroui/tooltip";
import { useSearchParams } from "next/navigation";
import { useRouter } from "next/navigation";
import ModalChartWard from "@/components/modal/ModalChartWard";
import Ripple from "@nuttawoot_donut/react-ripple";

export default function DashboardShowcase() {
  const { hnameStatus } = HospitalHook();
  const [data, setData] = useState({ countAll: 0, countWard: [], countPatientService: [] });
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState(null);
  const [ wardName, setWardName ] = useState("");
  const [ wardData, setWardData ] = useState([]);
  const [ VNData, setVNData ] = useState([]);
  const [ loadingWardData, setLoadingWardData ] = useState(false);
  const [ loadingVNData, setLoadingVNData ] = useState(false);
  const [ showModalWard, setShowModalWard ] = useState(false);
  const [ showModalVNCard, setShowModalVNCard ] = useState(false);

  const [ chartWardData, setChartWardData ] = useState({});
  const [ loadingChartWardData, setLoadingChartWardData ] = useState(false);

  const router = useRouter();
  const sp = useSearchParams();
  const chartWard = sp.get("chart");

  const ripple = new Ripple();

  const openChartWardModal = async (wardName) => {
    let hasWardName = await cryptoEncode(wardName);
    fetchChartWard(wardName, wardName === "OPD" && "vn");
    router.push(`/?chart=${encodeURIComponent(hasWardName)}`, { scroll: false });
  };
  const closeChartWardModal = () => {
    setChartWardData({});
    router.replace("/", { scroll: false });
  };
  
  let token = localStorage.getItem("token");

  useEffect(() => {
    fetchDashboard();
  }, []);
  
  const fetchDashboard = async () => {
    try {
      const res = await axiosApi.get("/dashboard");
      setData(res.data ?? {});
      setErr(null);
    } catch (e) {
      setErr(e);
    } finally {
      setLoading(false);
    }
  };

  const fetchDashboardWard = async (ward) => {
    setWardName("");
    setLoadingWardData(true);
    try {
      const res = await axiosApi.get(`/dashboard/${ward}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      setWardData(res.data.data ?? {});
      setWardName(ward);
    } catch (err) {
      console.log(err)
      addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    } finally {
      setLoadingWardData(false);
    }
  }

  const fetchDashboardVN = async (ward) => {
    setLoadingVNData(true);
    try {
      const res = await axiosApi.get(`/dashboard/fetchAllVn`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });

      if(res.status === 200) {
        let deHash = await cryptoDecode(res.data.data);
        setVNData(deHash);
      }
    } catch (err) {
      console.log(err)
      addToast({ title: "เกิดข้อผิดพลาด", description: err?.response?.data?.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    } finally {
      setLoadingVNData(false);
    }
  }

  if (err && !hnameStatus && !loading) {
    return (
      <ErrorBanner
        err={err}
        onRetry={fetchDashboard}      // ฟังก์ชันเดิมของนาย
        autoRetrySec={10}             // ไม่อยาก auto ก็ไม่ต้องส่ง
        onDismiss={() => setErr(null)}  // ฟังก์ชันเดิมของนาย
      />
    );
  } else if (hnameStatus) {
    return (
      <ErrorBanner
        err={
          {
            message: "เกิดข้อผิดพลาด 404",
            config: { "url": `/*` },
            response: {
              status: 404,
              data: {
              message: "ระบบตรวจพบว่าไม่มีการตั้งค่าชื่อโรงพยาบาล กรุณาตั้งค่าก่อนใช้งาน",
              errors: { 
                "Hcode": "ไม่มีการตั้งค่ารหัสโรงพยาบาล",
                "Hname": "ไม่มีการตั้งค่าชื่อโรงพยาบาล"
              }
             }
            }
          }
        }
      />
    )
  }

  const countPatientService = Object.entries(data)
  .filter(([k]) => k.includes("countPatientService"))
  .flatMap(([, v]) => v);

  const averagePatientService = Object.entries(data)
  .filter(([k]) => k.includes("averagePatientService"))
  .flatMap(([, v]) => v);

  const totalWardForms = useMemo(
    () => (Array.isArray(data.countWard) ? data.countWard.reduce((s, w) => s + (Number(w?.result) || 0), 0) : 0),
    [data.countWard]
  );

  const totalByService = useMemo(
    () => (Array.isArray(countPatientService) ? countPatientService.reduce((s, it) => s + (Number(it?.result) || 0), 0) : 0),
    [data.countPatientService]
  );

  const countAllPercentageNotNull = useMemo(() => {
    return Object.entries(data || {})
      .filter(([key]) => key.startsWith("countAllPercentageNotNull"))
      .reduce((sum, [, value]) => sum + Number(value || 0), 0);
  }, [data]);

  const countAllPercentageNull = useMemo(() => {
    return Object.entries(data || {})
      .filter(([key]) => key.startsWith("countAllPercentageNull"))
      .reduce((sum, [, value]) => sum + Number(value || 0), 0);
  }, [data]);

  const fetchChartWard = async (ward, type = "") => {
    setWardName(null);
    setLoadingChartWardData(true);

    let apiPath = type === "vn" ? `/dashboard/averageOPD` : `/dashboard/averageIPD/${ward}`

    try {
      const res = await axiosApi.get(`${apiPath}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      if (res.status === 200) {
        const payload = res.data?.data ?? res.data;
        const deHash = await cryptoDecode(payload);
        console.log(deHash)
        setChartWardData({
          wardAverage: deHash?.wardAverage ?? [],
          allAverage:  deHash?.allAverage  ?? []
        });
        setWardName(ward);
      }
    } catch (err) {
      console.log(err);
      addToast({ title: "เกิดข้อผิดพลาด", description: err?.response?.data?.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    } finally {
      setLoadingChartWardData(false);
    }
  };

  // ด้านบนมี chartWard แล้ว
  useEffect(() => {
    let alive = true;
    (async () => {
      if (!chartWard) return;
      try {
        const wardPlain = await cryptoDecode(chartWard);
        if (alive && wardPlain) await fetchChartWard(wardPlain, wardPlain === "OPD" && "vn");
      } catch (e) { console.error(e); }
    })();
    return () => { alive = false };
  }, [chartWard]);

  return (
    <div className="space-y-6 select-none">
      {/* แถวสถิติบนสุด */}
      <div className="grid gap-3 sm:grid-cols-2 lg:grid-cols-4">
        <StatCard
          icon={<ClipboardList className="size-5" />}
          label="จำนวนทั้งหมด"
          value={data?.countAll ?? 0}
          average={loading ? undefined : data?.averageAll?.[0]?.average ?? 0}
          loading={loading}
        />
        <StatCard
          icon={<Activity className="size-5" />}
          label="จำนวนตามประเภทบริการ"
          value={totalByService}
          hint={countPatientService?.map((s) => s.patient_service_name_english).join(" • ")}
          loading={loading}
        />
        <StatCard
          icon={<FileCheck2 className="size-5" />}
          label="ฟอร์มที่สมบูรณ์"
          value={countAllPercentageNotNull || 0}
          hint={`ทั้งหมด ${data.countAll || 0} ฟอร์ม`}
          loading={loading}
        />
        <StatCard
          icon={<FilePenLine className="size-5" />}
          label="ฟอร์มที่ยังไม่สมบูรณ์"
          value={countAllPercentageNull ?? 0}
          hint={`ทั้งหมด ${data.countAll || 0} ฟอร์ม`}
          loading={loading}
        />
      </div>

      {/* แถวล่าง: ซ้าย = รายตึก, ขวา = ตามบริการ */}
      <div className="grid gap-4 lg:grid-cols-3">
        {/* รายตึก */}
        <motion.div
          initial={{ opacity: 0, y: 8 }}
          animate={{ opacity: 1, y: 0 }}
          className="rounded-2xl border border-zinc-300 dark:border-[#505050] bg-white dark:bg-[#181818]"
        >
          <div className="flex items-center justify-between px-4 py-3 border-b border-zinc-200 dark:border-[#3a3a3a]">
            {loading ? (
              <Skeleton className="h-5 w-full max-w-32 rounded-md" />
            ) : (
              <p className="font-semibold">จำนวนฟอร์มของตึก</p>
            )}
            {loading ? (
              <Skeleton className="h-4 w-12 rounded-md" />
            ) : (
              <span className="text-xs text-zinc-500 dark:text-zinc-400">
                รวม {totalWardForms.toLocaleString("th-TH")}
              </span>
            )}
          </div>

          <div className="p-4 space-y-3">
            {Array.isArray(data.countWard) && data.countWard.length > 0 ? (
              data.countWard.map((w) => (
                <div className="grid grid-cols-4 gap-4">
                  <div className={`${token ? "col-span-3" : "col-span-4"}`}>
                    <WardRow 
                      key={w.patient_ward} 
                      name={w.patient_ward} 
                      value={w.result} 
                      total={totalWardForms || 1} 
                      token={token} 
                      averageWard={data.averageWard?.find(av => av.patient_ward === w.patient_ward)?.average || 0}
                      fetchDashboardWard={fetchDashboardWard} 
                      setShowModal={setShowModalWard}
                    />
                  </div>
                  {token && (
                    <Tooltip content={<p className="px-2">{w.patient_ward}</p>} showArrow>
                      <div 
                        className="cursor-pointer rounded-xl border backdrop-blur-2xl bg-white/5 border-zinc-200 dark:border-[#3a3a3a] p-3 shadow-lg shadow-zinc-950/5 dark:shadow-zinc-800/40 flex items-center justify-center relative"
                        onClick={() => {
                          openChartWardModal(w.patient_ward);
                        }}
                        onMouseDown={(e) => ripple.create(e)}
                      >
                        <div className="flex flex-col items-center gap-1">
                          <p className=" absolute top-0 right-0 text-xs text-zinc-600 dark:text-zinc-300 bg-zinc-200 dark:bg-zinc-700 py-0.5 px-2 rounded-full ">Chart</p>
                          <ChartColumn className="text-neutral-700 dark:text-neutral-200" />
                          <p className="text-neutral-700 dark:text-neutral-200 font-semibold text-sm line-clamp-1">{w.patient_ward}</p>
                        </div>
                      </div>
                    </Tooltip>
                  )}
                </div>
              ))
            ) : (
              <EmptyRow text={loading ? "กําลังโหลดข้อมูล..." : "ยังไม่มีข้อมูลตึก"} />
            )}
          </div>
        </motion.div>

        {/* ตามบริการ */}
        <motion.div
          initial={{ opacity: 0, y: 8 }}
          animate={{ opacity: 1, y: 0 }}
          className="lg:col-span-2 rounded-2xl border border-zinc-300 dark:border-[#505050] bg-white dark:bg-[#181818]"
        >
          <div className="flex items-center justify-between px-4 py-3 border-b border-zinc-200 dark:border-[#3a3a3a]">
            {loading ? (
              <Skeleton className="h-5 w-full max-w-32 rounded-md" />
            ) : (
              <p className="font-semibold">จำนวนฟอร์มตามประเภทบริการ</p>
            )}
            {loading ? (
              <Skeleton className="h-4 w-12 rounded-md" />
            ) : (
              <span className="text-xs text-zinc-500 dark:text-zinc-400">
                รวม {totalByService.toLocaleString("th-TH")}
              </span>
            )}
          </div>

          <div className="p-4 grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
            {Array.isArray(countPatientService) && countPatientService.length > 0 ? (
              countPatientService.map((s) => (
                <ServiceCard 
                  key={s.patient_service_name_english} 
                  name={s.patient_service_name_english} 
                  value={s.result} 
                  average={averagePatientService.find(av => av.patient_service_name_english === s.patient_service_name_english)?.average || 0} 
                  setShowModal={setShowModalVNCard} // show modal vn
                  fetchDashboardVN={fetchDashboardVN}
                  loading={loadingVNData}
                  openChartVNModal={() => openChartWardModal(s.patient_service_name_english)}
                />
              ))
            ) : (
              <div className="col-span-full">
                <EmptyRow text={loading ? "กําลังโหลดข้อมูล..." : "ยังไม่มีข้อมูลประเภทบริการ"} />
              </div>
            )}
          </div>
        </motion.div>
      </div>

      <AnimatePresence mode="sync">
        {showModalWard && (
          <ModalWardCard
            title="จำนวนฟอร์มของตึก"
            subtitle={wardName}
            key="ward"
            data={wardData}
            onClose={() => {
              setShowModalWard(false)
              setWardName("")
              setWardData([])
            }}
            loading={loadingWardData}
          />
        )}
      </AnimatePresence>

      <AnimatePresence mode="sync">
        {showModalVNCard && (
          <ModalVNCard
            title="จำนวน VN ของประเภทบริการ"
            key="ward"
            data={VNData}
            onClose={() => {
              setShowModalVNCard(false)
            }}
            loading={loadingVNData}
          />
        )}
      </AnimatePresence>

      <AnimatePresence>
        {chartWard && (
          <ModalChartWard
            key="chart"
            title="Chart Bar"
            data={chartWardData}
            onClose={closeChartWardModal}
            loading={loadingChartWardData}
            wardName={wardName}
            token={token}
          />
        )}
      </AnimatePresence>
    </div>
  );
}