import { cryptoEncode } from "@/config/crypto.mjs";
import { motion } from "framer-motion";
import Ripple from "@nuttawoot_donut/react-ripple";
import { useRouter } from "nextjs-toploader/app";
import { CircleCheckBig } from "lucide-react";

export default function WardCardDetail ({ data }) {

    const ripple = new Ripple();

    const router = useRouter();

    let finished = ((data?.form_ipd?.total_score || data?.total_score) && (data?.form_ipd?.score_obtained || data?.score_obtained) && (data?.form_ipd?.percentage || data?.percentage)) ? true : false;

    let percent = (data?.form_ipd?.percentage || data?.percentage) || 0;

    const hdlClick = async (id) => {
        let hashId = await cryptoEncode(id);
        if(id.length == 9){
            return router.replace(`/pdf/ipd/${encodeURIComponent(hashId)}`);
        } else {
            return router.replace(`/pdf/opd/${encodeURIComponent(hashId)}`);
        }
    }

    return (
        <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.25 }}
            key={data.form_ipd?.form_ipd_id}
            onClick={() => finished && hdlClick(data.patient_an || data?.patients?.patient_vn)}
            onMouseDown={(e) => finished && ripple.create(e)}
            className={`w-full rounded-xl border border-zinc-200 dark:border-[#3a3a3a] p-3 ${finished ? `cursor-pointer active:bg-white/5` : `cursor-auto`}`}
        >
            <div className="flex flex-col gap-2">
                <div className="flex justify-between">
                    <div className="flex flex-col">
                        <div className="flex gap-1 items-center">
                            <p className="font-bold">{(data.patient_an && `AN ${data.patient_an}`) || (data?.patients?.patient_vn && `VN ${data?.patients?.patient_vn}`)}</p>
                            <p className="text-xs line-clamp-1 w-[20%]" title={`${data?.patient_id}`} >({data.patient_id})</p>
                        </div>
                        <p className="text-xs text-zinc-500 dark:text-zinc-400">คะแนนรวม : {(data?.form_ipd?.total_score || data?.total_score) || 0} | คะแนนที่ได้ : {(data?.form_ipd?.score_obtained || data?.score_obtained) || 0} | ค่าร้อยละ : {(data?.form_ipd?.percentage || data?.percentage) || 0}%</p>
                    </div>

                    <p className={`text-xs font-semibold flex flex-col items-end h-fit px-4 py-1 rounded-full text-nowrap ${finished ? "bg-emerald-100 text-emerald-700 dark:bg-emerald-900 dark:text-emerald-200" : "bg-amber-100 text-amber-600 dark:bg-amber-900 dark:text-amber-200"}`}>{finished ? "สมบูรณ์" : "ยังไม่สมบูรณ์"}</p>
                </div>

                {/* progress */}
                <div className="my-1">
                    <div className="flex items-center justify-between text-xs mb-1">
                        <span className="text-zinc-500">คะแนนที่ได้คิดเป็น %</span>
                        <span className="font-medium">{percent == null ? "-" : `${percent}%`}</span>
                    </div>
                    <div className="h-2 rounded-full bg-zinc-100 dark:bg-zinc-700 overflow-hidden">
                    <div
                        className="h-full rounded-full bg-gradient-to-r from-emerald-500 to-emerald-600"
                        style={{ width: `${percent ?? 0}%` }}
                    />
                    </div>
                </div>

                {/* create by update by */}
                <div className="flex justify-between">
                    <p className="text-xs text-zinc-500 dark:text-zinc-400 line-clamp-2">ผู้สร้าง : {(data?.form_ipd?.created_by || data?.created_by)} | เมื่อ {new Date((data?.form_ipd?.created_at || data?.created_at)).toLocaleDateString("th-TH", { day: "numeric", month: "short", year: "numeric", hour: "numeric", minute: "numeric" })}</p>
                    <p className="text-xs text-zinc-500 dark:text-zinc-400 line-clamp-2">ผู้แก้ไขล่าสุด : {(data?.form_ipd?.updated_by || data?.updated_by)} | เมื่อ {new Date((data?.form_ipd?.updated_at || data?.updated_at)).toLocaleDateString("th-TH", { day: "numeric", month: "short", year: "numeric", hour: "numeric", minute: "numeric" })}</p>
                </div>
            </div>
        </motion.div>
    )
}