// ModalWardCard.tsx
"use client";
import { useEffect, useMemo, useState } from "react";
import { motion } from "framer-motion";
import { Building2, Maximize2, Minimize2, X } from "lucide-react";
import WardCardDetail from "../dashboard/WardCardDetail";
import Ripple from "@nuttawoot_donut/react-ripple";
import LoadingCenter from "../loading";
import HospitalHook from "@/hooks/HospitalHook.mjs";

const backdrop = {
  open: { opacity: 1, transition: { duration: 0.18 }, pointerEvents: "auto" },
  closed: { opacity: 0, transition: { duration: 0.18 }, pointerEvents: "none" }, // ← คลิกทะลุได้ตั้งแต่เริ่มปิด
};

const card = {
  open: { opacity: 1, scale: 1, y: 0, transition: { type: "spring", stiffness: 260, damping: 22 } },
  closed: { opacity: 0, scale: 0.96, y: 8, transition: { duration: 0.16 } },
};

export default function ModalWardCard({ title, subtitle, data = [], onClose, loading }) {
  const { hname, hcode } = HospitalHook();
  const ripple = new Ripple();
  const [isFull, setIsFull] = useState(false);

  // ESC + lock scroll
  useEffect(() => {
    const onKey = (e) => e.key === "Escape" && onClose();
    window.addEventListener("keydown", onKey);
    const prev = document.body.style.overflow;
    document.body.style.overflow = "hidden";
    return () => {
      window.removeEventListener("keydown", onKey);
      document.body.style.overflow = prev;
    };
  }, [onClose]);

  const compleateFormsLength = useMemo(
    () =>
      data.filter(
        (item) =>
          item.form_ipd?.percentage != null &&
          item.form_ipd?.total_score != null &&
          item.form_ipd?.score_obtained != null
      ).length,
    [data]
  );

  const notCompleateFormsLength = useMemo(
    () =>
      data.filter(
        (item) =>
          item.form_ipd?.percentage == null &&
          item.form_ipd?.total_score == null &&
          item.form_ipd?.score_obtained == null
      ).length,
    [data]
  );

  const scoreData = {
    compleateFormsLength,
    notCompleateFormsLength,
    totalFormsLength: data.length,
  }

  return (
    <motion.div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-[2px]"
      variants={backdrop}
      initial="closed"
      animate="open"
      exit="closed"
      onMouseDown={onClose}            // ← ปิดตั้งแต่ mousedown
    >
      <motion.div
        initial="closed" // ← คลิกทะลุได้ตั้งแต่เริ่มปิด 
        animate="open" // ← คลิกทะลุได้ตั้งแต่เริ่มปิด
        layout
        variants={card}
        onMouseDown={(e) => e.stopPropagation()} // ← กันคลิกไหลไปปิดเอง
        className={[
          "relative bg-white dark:bg-[#1f1f1f] shadow-2xl ring-1 ring-black/5",
          // สลับคลาสระหว่าง modal ปกติ vs เต็มจอ
          isFull
            ? "w-screen h-screen max-w-none rounded-none"
            : "w-full max-w-2xl rounded-2xl m-4",
        ].join(" ")}
      >
        {/* Header */}
        <div className="flex items-center justify-between border-b border-zinc-200 dark:border-zinc-700 px-4 py-2">
          <div>
            <p className="font-semibold">{title}</p>
            {!loading && 
              <p className="font-bold text-xs text-zinc-600 dark:text-zinc-400 flex items-center gap-1">
                <Building2 size={14} />
                {subtitle}
              </p>
            }
          </div>
          <div>
            <motion.button
              whileTap={{ scale: 0.98 }}
              onMouseDown={(e) => {
                ripple.create(e);
                setIsFull((v) => !v);
              }}
              className="cursor-pointer inline-flex h-8 w-8 items-center justify-center rounded-full hover:bg-black/5 dark:hover:bg-white/10"
              aria-label={isFull ? "Restore" : "Maximize"}
              title={isFull ? "ย่อกลับ" : "ขยายเต็มจอ"}
            >
              {isFull ? <Minimize2 size={18} /> : <Maximize2 size={18} />}
            </motion.button>
            { !isFull &&
            <motion.button
              whileTap={{ scale: 0.98 }}
              onMouseDown={(e) => {
                ripple.create(e);
                onClose();                 // ← ปุ่มปิดก็ใช้ mousedown
              }}
              className="cursor-pointer inline-flex h-8 w-8 items-center justify-center rounded-full hover:bg-black/5 dark:hover:bg-white/10"
              aria-label="Close"
            >
              <X size={18} />
            </motion.button> }
          </div>
        </div>

        {/* Body */}
        <motion.div 
          layout
          className={
            isFull
              // เต็มจอ: ให้สูงตาม viewport ลบ header+footer ประมาณ 6.5rem
              ? "px-4 py-2 overflow-y-auto h-[calc(100vh-6rem)]"
              // ปกติ: จำกัดความสูงเหมือนเดิม
              : "px-4 py-2 overflow-y-auto max-h-[calc(100vh-40vh)]"
          }
        >
          {!loading ? (
            <>
              {/* summary zone */}
              <div className="flex justify-between gap-2 mb-3 text-sm font-medium">
                <div className="flex gap-2">
                  <div className="bg-green-100 dark:bg-green-900/40 rounded-xl px-3 py-2">
                    <p className="text-green-700 dark:text-green-300">
                      สมบูรณ์ {scoreData.compleateFormsLength} ฟอร์ม
                    </p>
                  </div>
                  <div className="bg-amber-100 dark:bg-amber-900/40 rounded-xl px-3 py-2">
                    <p className="text-amber-700 dark:text-amber-300">
                      ยังไม่สมบูรณ์ {scoreData.notCompleateFormsLength} ฟอร์ม
                    </p>
                  </div>
                </div>
                <div className="bg-sky-100 dark:bg-sky-900/40 rounded-xl px-3 py-2">
                  <p className="text-sky-700 dark:text-sky-300">
                    รวมทั้งหมด {scoreData.totalFormsLength} ฟอร์ม
                  </p>
                </div>
              </div>

              {/* list zone */}
              <motion.div
                className={`grid ${isFull && "grid-cols-1 md:grid-cols-2 lg:grid-cols-3"} gap-2`}
              >
                {data.map((e, i) => (
                  <WardCardDetail
                    key={e?.form_ipd?.form_ipd_id ?? e?.patient_an ?? i}
                    data={e}
                  />
                ))}
              </motion.div>
            </>
          ) : (
            <div className="flex justify-center items-center my-6">
              <LoadingCenter title="กำลังโหลดข้อมูล..." />
            </div>
          )}
        </motion.div>

        {/* Footer */}
        <div className="text-xs text-zinc-400 flex items-center justify-end border-t border-zinc-200 dark:border-zinc-700 px-4 py-3">
          <p>{hname} {hcode}</p>
        </div>
      </motion.div>
    </motion.div>
  );
}
