import Ripple from "@nuttawoot_donut/react-ripple";
import { useState } from "react";
import PopUpMessage from "../modal/PopUpMessage";
import { AnimatePresence, motion } from "framer-motion";
import { ChartColumn, List } from "lucide-react";

export default function ServiceCard({ name, value, fetchDashboardVN, average, setShowModal, openChartVNModal }) {
  const [ popUpMessage, setPopUpMessage ] = useState(false);

  const rawToken = (typeof window !== "undefined" && localStorage.getItem("token")) || "";
  const hasToken = !!rawToken && rawToken !== "null" && rawToken !== "undefined";

  const disabled = name === "IPD" || !hasToken;

  const ripple = new Ripple();

  const hdlClick = () => {
    setPopUpMessage(true);
  }

  const hdlClickListVN = () => {
    setShowModal(true);
    fetchDashboardVN();
    setPopUpMessage(false);
  }

  const hdlClickShowChart = () => {
    openChartVNModal();
    setPopUpMessage(false);
  }

  return (
    <>
      <div
        onClick={disabled ? undefined : hdlClick}
        onMouseDown={disabled ? undefined : (e) => ripple.create(e)}
        className={`rounded-xl border flex justify-between border-zinc-200 dark:border-[#3a3a3a] p-3 bg-white/5 backdrop-blur-2xl shadow-lg shadow-zinc-950/5 dark:shadow-zinc-800/40 ${!disabled && "cursor-pointer"}`}>
        <div>
          <p className="text-sm text-zinc-500 dark:text-zinc-400 mb-1">{name}</p>
          <p className="text-xl font-semibold">{Number(value).toLocaleString("th-TH")}</p>
        </div>
        <div>
        { average ? 
          <div>
            <p className="text-xs text-zinc-500 dark:text-zinc-400 mb-1 text-right">ค่าเฉลี่ย</p>
            <p className="text-xl font-semibold">{Number(average).toLocaleString("th-TH")}</p>
          </div> 
          : null }
        </div>
      </div>

      <AnimatePresence>
        {popUpMessage &&
          <PopUpMessage
            type="info"
            title="ตัวเลือก"
            message={(
              <motion.div className="flex flex-col gap-3 justify-center items-center">
                <p className="text-base font-semibold">เลือกข้อมูลที่ต้องการดูของ OPD</p>
                <AnimatePresence>
                  <motion.div className="flex gap-4 items-center justify-around w-full">
                    <motion.button 
                      whileTap={{ scale: 0.98 }}
                      className="w-full text-white font-semibold bg-[#00B4A2] hover:bg-[#009C8D] rounded-xl px-4 py-1.5 flex gap-1 items-center justify-center cursor-pointer hover:shadow"
                      onClick={(e) => hdlClickShowChart(e)}
                      onMouseDown={(e) => ripple.create(e, "ligth")}
                    >
                      <ChartColumn strokeWidth={3} size={16} />ดูกราฟ
                    </motion.button>
                    <motion.button 
                      whileTap={{ scale: 0.98 }}
                      className="w-full font-semibold dark:bg-zinc-700 bg-white rounded-xl px-4 py-1.5 flex gap-1 items-center justify-center cursor-pointer hover:shadow"
                      onClick={(e) => hdlClickListVN(e)}
                      onMouseDown={(e) => ripple.create(e)}
                    >
                      <List strokeWidth={3} size={16} />ข้อมูล VN
                    </motion.button>
                  </motion.div>
                </AnimatePresence>
              </motion.div>
            )}
            onClose={() => setPopUpMessage(false)}
          />
        }
      </AnimatePresence>
    </>
  );
}