import { Skeleton } from "@heroui/react";
import { motion } from "framer-motion";

export default function StatCard({ icon, label, value, hint, average, loading }) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 6 }}
      animate={{ opacity: 1, y: 0 }}
      className="rounded-2xl border border-zinc-300 dark:border-[#505050] bg-white dark:bg-[#181818] p-4 shadow-lg shadow-zinc-950/5"
    >
      <div className="flex items-start gap-3">
        {/* icon */}
        {loading ? (
          <Skeleton className="h-8 w-8 rounded-xl bg-zinc-200 dark:bg-[#303030]" />
        ) : (
          <div className="rounded-xl bg-zinc-100 dark:bg-zinc-800 p-2.5">
            {icon}
          </div>
        )}

        {/* label + value + hint */}
        <div>
          {loading ? (
            <>
              <Skeleton className="h-3 w-20 mb-2 rounded-md" />
              <Skeleton className="h-6 w-24 mb-1 rounded-md" />
              <Skeleton className="h-3 w-32 rounded-md" />
            </>
          ) : (
            <>
              <p className="text-xs text-zinc-500 dark:text-zinc-400 line-clamp-1">
                {label}
              </p>
              <p className="text-2xl font-semibold leading-tight">
                {Number(value).toLocaleString("th-TH")}
              </p>
              {hint ? (
                <p className="text-xs text-zinc-400 mt-0.5">{hint}</p>
              ) : null}
            </>
          )}
        </div>

        {/* average */}
        {average !== undefined && (
          <div className="ml-auto text-right">
            {loading ? (
              <>
                <Skeleton className="h-3 w-12 mb-2 rounded-md" />
                <Skeleton className="h-6 w-20 rounded-md" />
              </>
            ) : (
              <>
                <p className="text-xs text-zinc-500 dark:text-zinc-400">
                  ค่าเฉลี่ย
                </p>
                <p className="text-2xl font-semibold leading-tight">
                  {Number(average).toLocaleString("th-TH")}
                </p>
              </>
            )}
          </div>
        )}
      </div>
    </motion.div>
  );
}
